module.exports = "/**\r\n *      (CS >= v54.1)\r\n */\r\ncc.getProfiles().then(function(result) {\r\n    console.log('success\\nresult:', result);\r\n\r\n    //     \r\n    fillOpenProfileSelectOptions(result);\r\n}, function(error) {\r\n    console.log('error', error);\r\n});\r\n\r\nfunction fillOpenProfileSelectOptions(profiles) {\r\n    var select = document.getElementById('open_profile_select');\r\n    \r\n    select.html = '';\r\n\r\n    var defaultOpt = document.createElement('option');\r\n    defaultOpt.value = 'My';\r\n    defaultOpt.innerHTML = 'Default: My';\r\n    select.appendChild(defaultOpt);\r\n\r\n    if (profiles['gost']) {\r\n        for (var i = 0; i < profiles['gost'].length; i++){\r\n            var opt = document.createElement('option');\r\n            opt.value = profiles['gost'][i];\r\n            opt.innerHTML = 'GOST: ' + opt.value;\r\n            select.appendChild(opt);\r\n        }\r\n    }\r\n    if (profiles['rsa']) {\r\n        for (var i = 0; i < profiles['rsa'].length; i++){\r\n            var opt = document.createElement('option');\r\n            opt.value = profiles['rsa'][i];\r\n            opt.innerHTML = 'RSA: ' + opt.value;\r\n            select.appendChild(opt);\r\n        }\r\n    }\r\n}\r\n"