module.exports = "//  \r\n\r\nvar decText = document.getElementById('decrypt_text_input').value;\r\nvar decFile = document.getElementById('decrypt_file_input').files[0];\r\n\r\nvar decData;\r\nif (decText !== \"\") {\r\n    decData = decText;\r\n} else {\r\n    decData = decFile;\r\n}\r\n\r\nvar options = {\r\n    flags: 0,\r\n    pkcs7: document.getElementById('decrypt_opt_pkcs7').checked\r\n};\r\n\r\n//  \r\ncc.decrypt(decData, options).then(function(result) {\r\n    var binSize = result.content.length / 4 * 3;\r\n    console.log('success [' + binSize + ' bytes]\\nresult content:', result.content);\r\n}, function(error) {\r\n    console.log('error', error);\r\n});"