module.exports = "var text = document.getElementById('sign_text_input').value;\r\nvar file = document.getElementById('sign_file_input').files[0];\r\n\r\nvar data;\r\nif (text !== \"\") {\r\n    data = new Blob([text]);\r\n} else {\r\n    data = file;\r\n}\r\n\r\nvar signatureFile = document.getElementById('sign_detached_signature_input').files[0];\r\n\r\nvar options = {\r\n    pkcs7: document.getElementById('sign_opt_pkcs7').checked,\r\n    detached: document.getElementById('sign_opt_detached').checked,\r\n    sendCertificate: document.getElementById('sign_opt_sendCertificate').checked,\r\n    sendChain: document.getElementById('sign_opt_sendChain').checked\r\n};\r\n\r\n//  \r\ncc.sign(data, options, signatureFile).then(function(result) {\r\n    var binSize = result.content.length / 4 * 3;\r\n    console.log('success [' + binSize + ' bytes]\\nresult content:', result.content);\r\n}, function(error) {\r\n    console.log('error', error);\r\n});\r\n"