module.exports = "//  ,     \r\n\r\nvar encText = document.getElementById('encrypt_text_input').value;\r\nvar encFile = document.getElementById('encrypt_file_input').files[0];\r\n\r\nvar data;\r\nif (encText !== \"\") {\r\n    data = new Blob([encText]);\r\n} else {\r\n    data = encFile;\r\n}\r\n\r\nvar options = {\r\n    flags: 0,\r\n    pkcs7: document.getElementById('encrypt_opt_pkcs7').checked,\r\n    receivers: String(document.getElementById('encrypt_receivers').value).split(',')\r\n};\r\n\r\n//  \r\ncc.encrypt(data, options).then(function(result) {\r\n    var binSize = result.content.length / 4 * 3;\r\n    console.log('success [' + binSize + ' bytes]\\nresult content:', result.content);\r\n}, function(error) {\r\n    console.log('error', error);\r\n});"